.UserScoreOut{
    font-family: Sans-serif;
font-size: 20;
color: #050505;
letter-spacing: 4px;
text-align: center;
font-weight: 500;
font-style: normal;
text-decoration: none;
width: auto;
margin: 7px 10%;
padding: 3px 3px;
border-radius: 20px;
background: #a4ffd5;
text-shadow: 7px 5px 14px #d6d3d3;


-webkit-animation: fadein 2s; /* Safari, Chrome and Opera > 12.1 */
-moz-animation: fadein 2s; /* Firefox < 16 */
 -ms-animation: fadein 2s; /* Internet Explorer */
  -o-animation: fadein 2s; /* Opera < 12.1 */
     animation: fadein 2s;
}   

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}