.InputText {
    margin-top: 10px;
    margin-bottom: 30px;
    padding: 5px;
    font-size: 16px;
    border-width: 1px;
    border-color: #8ddcdf;
    background-color: #FFFFFF;
    color: #000000;
    border-style: solid;
    border-radius: 0px;
    box-shadow: 0px 0px 11px rgba(66,66,66,.75);
    text-shadow: 0px 0px 5px rgba(66,66,66,.75);
    text-align: center;
}

.InputText:focus {
    outline:none;
}