.HighScoreList{
    text-align: center;
    align-items: center;
    width: auto;
    height: auto;


 
}

.TitleHighScore{
font-family: Comic Sans MS;
font-size: 45px;
color: #c72424;
letter-spacing: 4px;
font-weight: 600;
font-style:italic;
text-decoration: underline;
display: inline-block;
border-radius: 20px;
text-shadow: 7px 5px 14px #d6d3d3;
margin-bottom: 30px;
}

.activeList {
    overflow-y:scroll;
    height: 390px;   
    background-color:oldlace;
    margin : 5px 20px;
    
    -webkit-box-shadow: -2px 1px 13px 1px rgba(0,0,0,0.75); 
    box-shadow: -2px 1px 13px 1px rgba(0,0,0,0.75);
}

